.backgroundImage {
  width: 100%;
  height: 100%;
  background-image: url("https://www.indianpeopletimes.com/wp-content/uploads/2020/04/online-education-during-coronavirus.jpg");
  position: ABSOLUTE;
  top: 0;
  left: 0;
}
.loginFormWrapper {
  position: absolute;
  left: 51px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: #00bcd461;
  border-radius: 6px 74px;
  height: 300px;
  width: 200px;
}
.loginFormHeading {
  color: white;
  font-size: 27px;
  font-family: cursive;
  font-weight: bolder;
  margin: 20px;
}
.inputBoxWrapper {
  display: flex;
  flex-direction: column;
  margin: 10px;
}
.inputBox {
  height: 20px;
  font-variant: ruby;
  border-radius: 37px 19px;
  margin: 10px 0px;
}
.buttonWrapper {
  margin: 0px 10px;
  margin: 0px 10px;
  color: white;
  background: lightblue;
  font-weight: bold;
  font-size: 14px;
  border-radius: 9px;
}
